import * as React from "react";
import { PropsWithChildren } from "react";

type PropsBannerPhoto = PropsWithChildren<{
  // title: string;
  is_invert?: boolean;
}>;

const BannerPhoto: React.FC<PropsBannerPhoto> = (props: PropsBannerPhoto) => {
  const invert = props.is_invert ? "o-banner-photo--invert-gradient" : "";
  return <section className={`o-banner-photo ${invert} `}>{props.children}</section>;
};

export { BannerPhoto };
