import { graphql, PageProps } from "gatsby";
import React from "react";

import { Language } from "../../nubarchiva/settings/translations";
import Agreements from "../modules/content/Agreements/Agreements";
import BannerPhotoContact from "../modules/content/BannerPhoto/BannerPhotoContact";
import FAQs from "../modules/content/FAQs/FAQs";
import { Faq } from "../modules/content/FAQs/IFaqs";
import TitleResumeAndForm, { RenderForm } from "../modules/content/TitleResumeAndForm/TitleResumeAndForm";
import { CtaDemo, Hero } from "../modules/Hero/Hero";
import Layout from "../modules/Layout/Layout";
import { SEO } from "../modules/SEO/SEO";
import TitleMarkAndResume from "../modules/TitleMarkAndResume/TitleMarkAndResume";
import {ILink} from "../modules/Layout/header/navbarLinks/NavbarLinks";
import {IPageProps} from "../types/IData";

const GenericPage = ({ data }: IPageProps<Queries.GenericPageQuery>) => {
  return (
    <Layout
      lang={data.page?.lang as Language}
      landingName="generic"
      useLinks={data.allUse.nodes.map((item) => item.metas)}
      navbarLinksList={[...data.navbarLinks.nodes.map((item) => item.metas)]}
      legalLinks={data.allLegals?.nodes?.map((item) => item.childMarkdownRemark?.frontmatter) as ILink[] || []}
    >
      <Hero
        subtitle={data.page?.content?.hero?.subtitle}
        ctaDemo={data.page?.content?.hero?.ctaPopup ? CtaDemo.POPUP : CtaDemo.GO_TO_DEMO}
        backgroundImage={data.backgroundHero?.sharp?.gatsbyImageData}
        modifier={data.page?.settings?.id}
      >
        <h1 className={"hero__title"}>{data.page?.content?.hero?.title}</h1>
      </Hero>
      <TitleMarkAndResume titleMarkAndResume={{ ...data.page?.content?.main, tagTitle: "h2" }} nameSection={"main"} />
      <TitleResumeAndForm
        data={{ ...data.page?.content?.form, tagTitle: "h2" }}
        nameSection={"form-in-generic"}
        renderForm={data.page?.settings.id === "contact" ? RenderForm.INFO : RenderForm.DEMO}
      />
      <BannerPhotoContact />
      {data.page?.settings.id === "contact" ? <Agreements agreements={{ tagTitle: "h2" }} /> : <> </>}
      <FAQs
        title={data.page?.content?.faqs?.title}
        faqsLists={data.page?.content?.faqs?.FaqsList as Faq[]}
        tagTitle={"h2"}
      />
    </Layout>
  );
};

export default GenericPage;

export const Head = ({ data }: IPageProps<Queries.GenericPageQuery>) => (
  <SEO
    title={data.page?.metas.title}
    description={data.page?.metas.description}
    thumbnailWithExtension={data.page?.metas.thumbnail}
    canonical={data.page?.metas.slug}
    slug={data.page?.metas.slug}
    isNotOriginalContent={data.page?.metas.isNotOriginalContent}
    lang={data.page?.lang}
    hrefLangList={data.hrefLangsList?.nodes?.map((node) => ({
      hrefLang: node.lang,
      slug: node.metas.slug,
    }))}
  />
);

export const query = graphql`
  query GenericPage($id: String!, $settingsId: String!, $lang: String = "es_es") {
    page(id: { eq: $id }) {
      ...seo
      lang
      settings {
        id
      }
      content {
        hero {
          title
          subtitle
          ctaPopup
        }
        main {
          title
          resume_html
          mark_html
        }
        form {
          title
          resume_html
        }
        faqs {
          title
          FaqsList {
            answer
            question
          }
        }
      }
    }
    backgroundHero: file(name: { eq: $settingsId }, ext: { eq: ".jpg" }) {
      sharp: childImageSharp {
        gatsbyImageData
      }
      ext
    }
    navbarLinks: allPage(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          name
          slug
        }
      }
    }
    allUse(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          slug
          name
        }
        summary {
          title
          description
        }
      }
    }
    allLegals: allFile(
      filter: { sourceInstanceName: { eq: "legal" }, relativeDirectory: { eq: $lang } }
      sort: { childMarkdownRemark: { frontmatter: { order: ASC } } }
    ) {
      nodes {
        childMarkdownRemark {
          frontmatter {
            slug
            name: title
          }
          id
        }
        relativeDirectory
      }
    }
    hrefLangsList: allPage(filter: { settings: { id: { eq: $settingsId } } }) {
      nodes {
        metas {
          slug
        }
        lang
      }
    }
  }
`;
